
import Tab from 'vux/src/components/tab/tab.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Icon from 'vux/src/components/icon/index.vue'

import { listAllProcessApply, Processparticipate, listGoingProcessApply, completeAllList, ApplicationDetial, claimApplication, HandleApplication, CancelApplication, searchDepartment } from '@/api/index'
export default {
components: {
Tab,
TabItem,
ButtonTab,
ButtonTabItem,
CheckIcon,
XDialog,
Swipeout,
SwipeoutItem,
SwipeoutButton,
Loading,
Datetime,
Group,
Icon,
XSwitch
},
data () {
return {
startTime: '',
endTime: '',
loading: false,
imgPriview: false, // 图片预览
imgUrl: '', // 预览图片地址
fileUrl: '', // 文件地址
applyInfo: false, // 申请详情弹框
applyType: false,
navbar: false,

tabType: 1,
leaveList: [],

task: {
type: {}
}, // 任务类型
reason: '',
selectApplyInfo: {}, // 当前申请的详情
listQuery: {
pageNum: 1,
pageSize: 100
},
form: {
accompanyId: '',
actualLoanTime: '',
actualReturnTime: '',
reason: ''
},
value: '',
cancel: null,
results: [],
isShowSelect: true,
notdata: false,
upLoading: true,
resLoading: true,
special: true
}
},
mounted () {
this.listAllProcessApply()
},
methods: {
scroll () {


























},
searchDepartment (query) {
this.isShowSelect = true
searchDepartment({ searchName: query }).then(res => {
this.results = res.result
if (this.results.length === 0) {
this.notdata = true
} else {
this.notdata = false
}
})
},
searchEvent (val) {
if (this.timer) {
clearTimeout(this.timer)
}
if (val) {
this.timer = setTimeout(() => {
this.searchDepartment(this.value)
}, 300)
} else {

this.searchDepartment(this.value)
}
},
checkaccompany (item) {
this.value = item.name
this.form.accompanyId = item.companyId
this.isShowSelect = false
},
changeLoanTime (val) {
this.form.actualLoanTime = val + ':00'
},
changeReturnTime (val) {
this.form.actualReturnTime = val + ':00'
},
formaterstatus (status) {
var result = ''
if (status === 11) {
result = '用章'
} else if (status === 12) {
result = '借章'
} else if (status === 21) {
result = '复印件'
} else if (status === 22) {
result = '原件'
}
return result
},
formaterType (type) {
var result = ''
if (type === 1) {
result = '正本复印件'
} else if (type === 2) {
result = '副本复印件'
} else if (type === 3) {
result = '正本扫描件'
} else if (type === 4) {
result = '副本扫描件'
}
return result
},
formaterDo (value) {
var result = ''
if (value === 0) {
result = '进行时'
} else if (value === 1) {
result = '完成'
} else if (value === 2) {
result = '放弃'
} else if (value === 3) {
result = '过期'
} else if (value === 4) {
result = '驳回'
}
return result
},

showImg (url) {

var fileType = url.substring(url.lastIndexOf('.') + 1)
if (fileType === 'jpg' || fileType === 'png' || fileType === 'GIF' || fileType === 'jpeg') {
this.imgUrl = url
this.fileUrl = ''
this.imgPriview = true
} else {
this.fileUrl = url
this.imgUrl = ''
window.open(this.fileUrl, '_blank')
}
this.applyInfo = false
},




cancelTask (processInstanceId) {
const data = {
processInstanceId: processInstanceId
}
const that = this
this.$vux.confirm.show({
title: '提示',
content: '是否取消申请',
onConfirm () {
CancelApplication(data)
.then(res => {
that.$vux.toast.show({
text: res.message,
type: 'success'
})
that.listGoingProcessApply()
})
}
})
},

claimTask (item) {
const that = this
const data = {
taskId: item.taskId
}
this.$vux.confirm.show({
title: '提示',
content: '是否先进行签收',
onConfirm () {
claimApplication(data).then(res => {
that.listAllProcessApply()
})
}
})
},









listAllProcessApply () {
this.loading = true
listAllProcessApply(this.listQuery)
.then(res => {
this.loading = false
let list = res.result.list
this.$nextTick(() => {
this.leaveList = list
})
})
},

Processparticipate () {
this.loading = true
Processparticipate(this.listQuery).then(res => {
this.loading = false
let list = res.result.list
this.$nextTick(() => {
this.leaveList = list
})
})
},

listGoingProcessApply () {
this.loading = true
listGoingProcessApply(this.listQuery)
.then(res => {
this.loading = false
let list = res.result.list
this.$nextTick(() => {
this.leaveList = list
})
})
},

completeAllList () {
this.loading = true
completeAllList(this.listQuery)
.then(res => {
this.loading = false
let list = res.result.list
this.$nextTick(() => {
this.leaveList = list
})
})
},

















































































navbarChange () {
this.navbar = !this.navbar
},
tabClick (val) {




console.log(this.$refs.viewBox.scrollTop)
if (val === this.tabType) {
return false
}
this.tabType = val
this.closeSwipeoutOpen()
if (this.tabType === 1) {
this.listAllProcessApply()
} else if (this.tabType === 2) {
this.Processparticipate()
} else if (this.tabType === 3) {
this.listGoingProcessApply()
} else {
this.completeAllList()

}

},
closeSwipeoutOpen () {
if (this.$refs.swipeoutItem) {
this.$refs.swipeoutItem.forEach(e => {
if (e.isOpen) {
e.close()
}
})
}
},
showDoing (item) {
this.task = item
},

ApplicationDetial (item) {
ApplicationDetial({processInstanceId: item.processInstanceId}).then(res => {
this.applyInfo = true
this.selectApplyInfo = res.result
})
},
showApplyInfo (item) {
this.form.reason = ''

if (this.tabType === 1) {

if (item.claimUserId == null) {
this.claimTask(item)
} else {
this.ApplicationDetial(item)
}
} else {
this.ApplicationDetial(item)
}
},

submitApply (status) {
const data = {
pass: status,
reason: this.form.reason,
processInstanceId: this.selectApplyInfo.processInstanceId
}
if (this.selectApplyInfo.formKey && this.selectApplyInfo.formKey === 'setLoanTime') {
data.actualLoanTime = this.form.actualLoanTime
} else if (this.selectApplyInfo.formKey && this.selectApplyInfo.formKey === 'setReturnTime') {
data.actualReturnTime = this.form.actualReturnTime
} else if (this.selectApplyInfo.formKey && this.selectApplyInfo.formKey === 'setAccompany') {
data.accompanyId = this.form.accompanyId
} else if (this.selectApplyInfo.formKey && this.selectApplyInfo.formKey === 'approver') {
data.special = this.special
}
console.log(data)
HandleApplication(data).then(res => {
this.$vux.toast.show({
text: res.message,
type: 'success'
})
this.applyInfo = false
this.listAllProcessApply()
})
},

gotoApplyLeave () {
this.$router.push({
path: '/applyLeave'
})
},

gotoOverTime () {
this.$router.push({
path: '/overTimeLeave'
})
},

gotoReportLeave () {
this.$router.push({
path: '/reportLeave'
})
},

gotoAbnormal () {
this.$router.push({
path: '/abnormal'
})
}
}
}



